import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			addressData: {
				name: '',
				mobile: '',
				idCard: '',
				default: '',
			},
		}
	},
	onLoad() {
		
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {

		switchChange(e) {
			if(e.detail.value){
				this.addressData.default = '001'
			}
			else{
				this.addressData.default = '002'
			}
			console.log(this.addressData)
		},
		//提交
		confirm() {
			let data = this.addressData;
			
			if (!data.name) {
				this.$api.msg('请填写联系人姓名');
				return;
			}
			if (!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.mobile)) {
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			uni.request({
				url: this.api + '/api/saveUserContact',
				data: {
					token: this.userInfo.token,
					contactName: data.name,
					phone: data.mobile,
					idCard: data.idCard,
					defaultName: data.default,
				},
				success: res => {
					console.log(res)
					if (res.data.code == 1) {
						this.$api.msg('新增成功');
						setTimeout(() => {
							uni.navigateTo({
								url: '/pages/address/address'
							});
						}, 1500)
					}
				},
			});
			
		},
	}
}
